import esArrayIterator from 'core-js/modules/es.array.iterator';
import icons from 'url:../../img/icons.svg';

export default class View {
    _data;  

    render(data, render= true){
        if(!data || Array.isArray(data) && data.length === 0) return this.renderError(this._errorMessage);
        this._data = data;
        const markup = this._generateMarkup(this._data);

        if(!render) return markup;
        this._clear();
        this._parentElement.insertAdjacentHTML("afterbegin",markup);
         
    }

    update(data){
        this._data = data;
        const newMarkup = this._generateMarkup(this._data);
        const newDom = document.createRange().createContextualFragment(newMarkup);
        const newElements = Array.from(newDom.querySelectorAll("*"));
        const curElement = Array.from(this._parentElement.querySelectorAll('*'));
        newElements.forEach((newEl, i) => {
          const curEl = curElement[i];
          if(!newEl.isEqualNode(curEl) && newEl.firstChild?.nodeValue.trim() !== ''){
            curEl.textContent = newEl.textContent;
          };
          if(!newEl.isEqualNode(curEl)){
            Array.from(newEl.attributes).forEach(attr => curEl.setAttribute(attr.name, attr.value))
          }

        })
    }

    _clear(){
        this._parentElement.innerHTML = '';
    } 
    
    renderSpinner(){
        const markup = `
          <div class="spinner">
            <svg>
              <use href="${icons}_icon-loader"></use>
            </svg>
          </div>
        `;
        this._clear();
        this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }

    renderError(message=this._errorMessage){
        const markup = `
          <div class="error">
            <div>
              <svg>
                <use href="${icons}_icon-alert-triangle"></use>
              </svg>
            </div>
            <p>${message}</p>
          </div> 
        `
        this._clear();
        this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }

    renderMessage(message= this._message){
        const markup = `
          <div class="error">
            <div>
              <svg>
                <use href="${icons}_icon-smile"></use>
              </svg>
            </div>
            <p>${message}</p>
          </div> 
        `
        this._clear();
        this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }

}